* {
  box-sizing: border-box;
}

.movies_movie {
  background-color: white;
  border: 1px solid black;
  margin: 40px auto;
  padding: 15px 15px;
  width: 45%;
  border-radius: 20px;
  text-align: center;
}

.movies_data {
  display: flex;
  flex-direction: column;
}

.movies_movie img {
  width: 40%;
  position: relative;
  top: -50px;
}

.movies_movie .movie_title {
  font-size: 20px;
}

.movies_movie .movie_genres {
  list-style: none;
  padding-left: 0;
  opacity: 0.6;
}
